<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="分类名称：">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="分类名称"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="showAddFirstDia = true">新增分类</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <!-- @expand-change="rowClick" -->
        <el-table :data="list"  style="width: 100%" :row-key="getRowKeys" :expand-row-keys="expands">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-table :data="props.row.productCategoryShopList" border style="width: 100%">
                <el-table-column prop="name" label="分类名称"></el-table-column>
                <el-table-column label="分类图标">
                  <template slot-scope="scope">
                    <el-image style="width: 100px; height: 100px" :src="scope.row.imageUrl" fit="fill"></el-image>
                  </template>
                </el-table-column>
                <el-table-column prop="productCount" label="商品数量"></el-table-column>
                <el-table-column label="移动">
                  <template slot-scope="scope">
                    <!-- <i v-show="scope.$index != 0" class="el-icon-upload2 icon" @click="moveSecond(props.row,scope.$index,1)"></i> -->
                    <i v-show="scope.$index != 0" class="el-icon-top icon ml" @click="moveSecond(props.row,scope.$index,2)"></i>
                    <i v-show="scope.$index != props.row.productCategoryShopList.length -1" class="el-icon-bottom icon ml" @click="moveSecond(props.row,scope.$index,3)"></i>
                    <!-- <i v-show="scope.$index != props.row.productCategoryShopList.length -1" class="el-icon-download icon ml" @click="moveSecond(props.row,scope.$index,4)"></i> -->
                    <span v-show="scope.$index == 0 && props.row.productCategoryShopList.length == 1">-</span>
                  </template>
                </el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button @click="showEditSecondDiafun(scope.row,props.row)" type="text">编辑</el-button>
                    <el-button @click="deleteItem(scope.row)" type="text">删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </template>
          </el-table-column>
          <el-table-column label="分类名称" prop="name"></el-table-column>
          <el-table-column label="创建时间" prop="createTime"></el-table-column>
          <el-table-column label="商品数量" prop="productCount"></el-table-column>
          <el-table-column label="移动">
            <template slot-scope="scope">
              <!-- <i v-show="scope.$index != 0" class="el-icon-upload2 icon" @click="moveFirst(scope.$index,1)"></i> -->
              <i v-show="scope.$index != 0" class="el-icon-top icon ml" @click="moveFirst(scope.$index,2)"></i>
              <i v-show="scope.$index != list.length -1" class="el-icon-bottom icon ml" @click="moveFirst(scope.$index,3)"></i>
              <!-- <i v-show="scope.$index != list.length -1" class="el-icon-download icon ml" @click="moveFirst(scope.$index,4)"></i> -->
              <span v-show="scope.$index == 0 && list.length == 1">-</span>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button @click="showAddSecondDiafun(scope.row)" type="text">新增二级分类</el-button>
              <el-button @click="editFirstShow(scope.row)" type="text">编辑</el-button>
              <el-button @click="deleteItem(scope.row)" type="text">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </div>
    <!-- 添加一级 -->
    <el-dialog title="添加一级分类" :visible.sync="showAddFirstDia" width="30%">
      <el-form :model="firstForm" ref="firstForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="一级分类名称：" required>
          <el-input @input="change($event)" v-model.trim="firstForm.name" maxlength="5" show-word-limit></el-input>
        </el-form-item>
        <el-form-item>
          <el-button :loading="firstFormLoading" type="primary" @click="addFirst('firstForm')">确 认</el-button>
          <el-button @click="resetForm('firstForm')">取 消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 编辑一级 -->
    <el-dialog title="编辑一级分类" :visible.sync="showEditFirstDia" width="30%">
      <el-form :model="firstEditForm" ref="firstEditForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="一级分类名称：">
          <el-input @input="change($event)" v-model="firstEditForm.name" maxlength="5" show-word-limit></el-input>
        </el-form-item>
        <el-form-item>
          <el-button :loading="firstEditFormLoading" type="primary" @click="editFirst('firstEditForm')">确 认</el-button>
          <el-button @click="resetForm('firstEditForm')">取 消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 添加二级 -->
    <el-dialog title="添加二级分类" :visible.sync="showAddSecondDia" width="30%">
      <el-form :model="secondForm" :rules="rules" ref="secondForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="一级分类名称：">
          <div>{{secondForm.parentName}}</div>
        </el-form-item>
        <el-form-item label="二级分类名称：" requeired>
          <el-input @input="change($event)" v-model="secondForm.name" maxlength="10" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="二级分类图片：" required>
          <el-button :disabled="secondForm.showImg" type="primary" @click="showImgMaterialDia('add')">添加图片<i class="el-icon-upload el-icon--right"></i></el-button>
          <span style="font-size:14px;color:#909399;">建议尺寸198*198px</span>
        </el-form-item>
        <el-form-item v-if="secondForm.showImg" label="" required>
          <el-popover placement="right" trigger="hover">
            <el-button @click.stop="deleteImg('add')" type="text" style="color:#909399;" icon="el-icon-circle-close">删除</el-button>
            <el-image slot="reference" style="width: 100px; height: 100px" :src="secondForm.imageUrl" fit="fill"></el-image>
          </el-popover>
        </el-form-item>
        <el-form-item>
          <el-button :loading="secondFormLoading" type="primary" @click="addSedcond('secondForm')">确 认</el-button>
          <el-button @click="resetForm('secondForm')">取 消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 编辑二级 -->
    <el-dialog title="编辑二级分类" :visible.sync="showEditSecondDia" width="30%">
      <el-form :model="secondEditForm" :rules="rules" ref="secondEditForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="一级分类名称：">
          <div>{{secondEditForm.parentName}}</div>
        </el-form-item>
        <el-form-item label="二级分类名称：" prop="name">
          <el-input @input="change($event)" v-model="secondEditForm.name" maxlength="10" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="二级分类图片：" required>
          <el-button :disabled="secondEditForm.showImg" type="primary" @click="showImgMaterialDia('edit')">添加图片<i class="el-icon-upload el-icon--right"></i></el-button>
          <span style="font-size:14px;color:#909399;">建议尺寸198*198px</span>
        </el-form-item>
        <el-form-item v-if="secondEditForm.showImg" label="" required>
          <el-popover placement="right" trigger="hover">
            <el-button @click.stop="deleteImg('edit')" type="text" style="color:#909399;" icon="el-icon-circle-close">删除</el-button>
            <el-image slot="reference" style="width: 100px; height: 100px" :src="secondEditForm.imageUrl" fit="fill"></el-image>
          </el-popover>
        </el-form-item>
        <el-form-item>
          <el-button :loading="secondEditFormLoading" type="primary" @click="editSedcond('secondEditForm')">确 认</el-button>
          <el-button @click="resetForm('secondEditForm')">取 消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 图片库 -->
    <el-dialog title="图片库" :visible.sync="showImgMaterial" width="70%">
      <imageMaterial
        :limitType="true"
        :getPost="getPost"
        :multiple="false"
        :totalNum="1"
        :selectNum="0"
        @onlyclose="onlyclose"
        @subMit="subMit"
      ></imageMaterial>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import imageMaterial from '@/components/shopMaterial/imageMaterial';
import { 
  getProductCategoryInfoList,
  insertProductCategoryShop,
  isCategoryNameExist,
  deleteProductCategoryShop,
  updateProductCategoryShopOrderNo,
  updateProductCategoryShop
} from '@/api/commodity/commodityClassify'
export default {
  data(){
    return{
      showImgMaterial:false,
      showImgMaterialType:'',
      getPost:false,
      searchList:{
        name:''
      },
      dataLoading:false,
      list:[],

      showAddFirstDia:false,        // 添加一级弹出框
      firstForm:{},
      firstFormLoading:false,     

      showEditFirstDia:false,        // 添加一级弹出框
      firstEditForm:{},
      firstEditFormLoading:false, 

      showAddSecondDia:false,       // 添加二级弹出框
      secondFormLoading:false,
      secondForm:{
        showImg:false
      },
      showImg:false,

      showEditSecondDia:false,       // 添加二级弹出框
      secondEditFormLoading:false,
      secondEditForm:{
        showImg:false
      },
      rules: {
        name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ],
      },
      expands:[],
      
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData();
  },
  methods:{
    // 新增二级分类弹出框
    showAddSecondDiafun(e){
      console.log('e',e)
      this.secondForm.parentId = e.id
      this.secondForm.parentName = e.name
      this.showAddSecondDia = true
    },
    // 添加二级分类
    addSedcond(formName){
      if(!this.secondForm.name){
        this.$message.error('请正确输入二级分类名称');
        return;
      }
      if(!this.secondForm.imageUrl){
        this.$message.error('请上传二级分类图片后再试');
        return;
      }
      this.secondFormLoading = true;
      this.secondForm.shopId = this.shop.shopId
      this.secondForm.level = 2
      console.log('添加二级分类请求的数据',this.secondForm)
      this.checkName(this.secondForm).then((res)=>{
        console.log('有没有名字',res)
        if(res.code == 200){
          this.add(this.secondForm,formName);
        }else{
          this.secondFormLoading = false;
          this.$message.error(res.message);
        }
      })
    },

    // 编辑二级分类弹出框
    showEditSecondDiafun(e,e1){
      console.log('e',e,e1)
      this.secondEditForm.parentId = e1.id
      this.secondEditForm.parentName = e1.name
      this.secondEditForm.showImg = true
      this.secondEditForm.imageUrl = e.imageUrl
      this.secondEditForm.name = e.name
      this.secondEditForm.oldname = e.name
      this.secondEditForm.id = e.id
      this.showEditSecondDia = true
    },
    // 添加二级分类
    editSedcond(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(!this.secondEditForm.imageUrl){
            this.$message.error('请上传二级分类图片后再试');
            return;
          }
          this.secondEditFormLoading = true;
          this.secondEditForm.shopId = this.shop.shopId
          this.secondEditForm.level = 2
          if(this.secondEditForm.oldname == this.secondEditForm.name){
            this.edit(this.secondEditForm,formName);
          }else{
            this.checkName(this.secondEditForm).then((res)=>{
              console.log('有没有名字',res)
              if(res.code == 200){
                this.edit(this.secondEditForm,formName);
              }else{
                this.secondEditFormLoading = false;
                this.$message.error(res.message);
              }
            })
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 一级 - 挪位置
    moveFirst(n,n1){
      let obj = {};
      obj.id = this.list[n].id
      let i ;
      if(n1 == 1) i = 0
      if(n1 == 2) i = n-1
      if(n1 == 3) i = n+1
      if(n1 == 4) i = this.list.length -1
      obj.chanageId = this.list[i].id
      this.move(obj)
    },
    // 二级 - 挪位置
    moveSecond(o,n,n1){
      let obj = {};
      obj.id = o.productCategoryShopList[n].id
      let i ;
      if(n1 == 1) i = 0
      if(n1 == 2) i = n-1
      if(n1 == 3) i = n+1
      if(n1 == 4) i = o.productCategoryShopList.length -1
      obj.chanageId = o.productCategoryShopList[i].id
      this.move(obj)
    },
    move(obj){
      updateProductCategoryShopOrderNo(obj).then(res=>{
        console.log('移动',res)
        if(res.code == 200){
          this.$message({
            message: '移动成功',
            type: 'success'
          });
          this.getData();
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 删除分类
    deleteItem(e){
      if(e.productCount != 0){
        this.$confirm('批量修改分组后，便可删除', '当前有商品在该分组下，无法删除', {
          confirmButtonText: '确定',
          cancelButtonText: '去修改',
          type: 'warning'
        }).then(() => {}).catch(() => {
          let details = this.$router.resolve({
            name: "商品列表",
          });
          console.log('details',details)
          window.open(details.href, "_blank")
        });
        return;
      }
      deleteProductCategoryShop({id:e.id}).then(res=>{
        console.log('删除分类',res)
        if(res.code == 200){
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.getData();
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 请求验证是否重名
    checkName(e){
      return new Promise((resolve, reject) => {
        isCategoryNameExist(e).then(res=>{
          console.log('验证名字',res)
          if(res.code ==200){
            resolve(res)
          }else{
            this.firstFormLoading = false;
            this.firstEditFormLoading = false;
            this.secondFormLoading = false;
            this.secondEditFormLoading = false;
            this.$message.error(res.message);
          }
          
        })
      })
    },
    //  添加分类 
    add(obj,name){
      insertProductCategoryShop(obj).then(res=>{
        console.log('添加分类',res)
        if(res.code == 200){
          this.$message({
            message: '添加成功',
            type: 'success'
          });
          this.getData();
          if(name == 'firstForm'){
            this.firstForm.name = ''
            this.showAddFirstDia = false;
          }
          if(name == 'secondForm'){
            this.showAddSecondDia = false;
            this.secondForm.imageUrl = '';
            this.secondForm.showImg = false
            this.secondForm.name = ''
          }
          setTimeout(()=>{
            this.firstFormLoading = false;
            this.secondFormLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
          this.firstFormLoading = false;
          this.secondFormLoading = false;
        }
      })
    },
    // 添加一级分类
    addFirst(formName) {
      if(!this.firstForm.name){
        this.$message.error('请输入分类名称');
        return;
      }
      this.firstFormLoading = true;
      this.firstForm.shopId = this.shop.shopId
      this.firstForm.level = 1
      this.firstForm.parentId = 0
      this.checkName(this.firstForm).then((res)=>{
        console.log('有没有名字',res)
        if(res.code == 200){
          this.add(this.firstForm,formName);
        }else{
          this.firstFormLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    //  编辑分类 
    edit(obj,name){
      updateProductCategoryShop(obj).then(res=>{
        console.log('编辑分类',res)
        if(res.code == 200){
          this.$message({
            message: '添加成功',
            type: 'success'
          });
          this.getData();
          if(name == 'firstEditForm'){
            this.showEditFirstDia = false;
            this.firstEditForm.name = ''
          } 
          if(name == 'secondEditForm'){
            this.showEditSecondDia = false;
            this.secondEditForm.imageUrl = '';
            this.secondEditForm.showImg = false
            this.secondEditForm.name = ''
          }
          setTimeout(()=>{
            this.firstEditFormLoading = false;
            this.secondEditFormLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
          this.firstEditFormLoading = false;
          this.secondEditFormLoading = false;
        }
      })
    },
    // 编辑一级弹出框
    editFirstShow(e){
      this.firstEditForm.id = e.id
      this.firstEditForm.name = e.name
      this.firstEditForm.oldname = e.name
      this.showEditFirstDia = true
    },
    // 编辑一级分类
    editFirst(formName) {
      if(!this.firstEditForm.name){
        this.$message.error('请输入分类名称');
        return;
      }
      if(this.firstEditForm.oldname == this.firstEditForm.name){
        this.$message({
          message: '编辑成功',
          type: 'success'
        });
        this.showEditFirstDia = false;
        return;
      }
      this.firstEditFormLoading = true;
      this.firstEditForm.shopId = this.shop.shopId
      this.firstEditForm.level = 1
      this.firstEditForm.parentId = 0
      this.checkName(this.firstEditForm).then((res)=>{
        console.log('有没有名字',res)
        if(res.code == 200){
          this.edit(this.firstEditForm,formName);
        }else{
          this.firstEditFormLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 二级分类 - 删除图片
    deleteImg(e){
      if(e == 'add'){
        this.secondForm.showImg = false;
        this.secondForm.imageUrl = ''
      }
      if(e == 'edit'){
        this.secondEditForm.showImg = false;
        this.secondEditForm.imageUrl = ''
      }
      
    },
    // 关闭弹框
    resetForm(formName) {
      if(formName == 'firstForm') this.showAddFirstDia = false;
      if(formName == 'secondForm') this.showAddSecondDia = false;
      if(formName == 'firstEditForm') this.showEditFirstDia = false;
      if(formName == 'secondEditForm') this.showEditSecondDia = false;
    },
    // 获取列表
    getData(){
      getProductCategoryInfoList(this.searchList).then(res=>{
        console.log('店铺分类列表',res)
        this.list = res.data.list
      })
    },
    // 重置
    reset(){
      this.searchList.name = ''
      this.getData()
    },
    change(e){
      this.$forceUpdate()
    },
    // 自动展开点击行
    expandChange(row) {
      Array.prototype.remove = function(val) {
        let index = this.indexOf(val);
        if (index > -1) {
          this.splice(index, 1);
        }
      };
        if (this.expands.indexOf(row.id) < 0) {
        this.expands = [];
      }else {
        this.expands.remove(row.id);
        this.expands = [];
      }
    },
    rowClick(row,expandedRows,expanded) {
      //点击列展开行
      this.expandChange(row);
    },
    getRowKeys(row) {
      return row.id;
    },
    /**
    * 图片库操作方法
    */
    showImgMaterialDia(e){
      this.getPost = true;
      this.showImgMaterial = true;
      this.showImgMaterialType = e
    },
    onlyclose(e){
      console.log('值点了关闭',e)
      this.showImgMaterial = false;
      this.getPost = false;
    },
    subMit(e){
      console.log('点击了确定',e)
      this.showImgMaterial = false;
      this.getPost = false;
      if(this.showImgMaterialType == 'add'){
        this.secondForm.showImg = true;
        this.secondForm.imageUrl = e[0].imageUrl
      }
      if(this.showImgMaterialType == 'edit'){
        this.secondEditForm.showImg = true;
        this.secondEditForm.imageUrl = e[0].imageUrl
      }
      
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
  components: {
    imageMaterial,
  },
}
</script>
<style lang="less" scoped>
  .pageDown{
    padding: 20px 0;
  }
  .icon{
    cursor: pointer;
    &:hover{
      color: #409EFF;
    }
    &.ml{
      margin-left: 10px;
    }
    .mr{
      margin: 0 10px;
    }
  }
</style>