import request from '@/utils/request'
// 查询分组
export function getGalleryByShopId (obj) {
  return request({
    url: `/product/business/gallery/getGalleryByShopId?shopId=${obj.shopId}&galleryName=${!obj.galleryName ? '' : obj.galleryName}&type=${obj.type}`,
    method: 'get',
  })
}
// 图片库 - 添加分组
export function insertGallery(data) {
  return request({
    url: `/product/business/gallery/insertGallery`,
    method: 'post',
    data: data
  })
}
